// import coruscantImg from "../img/coruscant.png"
import Model from "./Model"
import {Category1Button, Category2Button, Category3Button} from "./CategoryButton"

function Coruscant() {
    return(
        <div className="container">
            <div id="topContainer">
                {/* <img class="planetImg" src={coruscantImg}/> */}
                <Model/>
                <div id="textDiv">
                    <h1>CORUSCANT</h1>
                    <p className="planetinfo">Also known as Imperial Center during the rule of the Galactic Empire, was an <abbr title="a city-covered planet">ecumenopolis</abbr>, collectively known as Imperial City in the Coruscant system of the Core Worlds. Though debated by historians, it was generally believed that Coruscant was the original homeworld of humanity.</p>
                    <p id="planetlink">link <a href="https://starwars.fandom.com/wiki/Coruscant">Wookieepedia</a></p>
                    <div id="butns">
                        <a id="category1" className="categoryButton categoryButtonPressed" href="" onclick={Category1Button}>01 Overview</a>
                        <a id="category2" className="categoryButton" href="" onclick={Category2Button}>02 </a>
                        <a id="category3" className="categoryButton" href="" onclick={Category3Button}>03</a>
                    </div>
                </div>
            </div>
            <div id="bottomContainer">
                <div className="bottomText">
                    <h2>ROTATION TIME</h2>
                    <p>24 HOURS</p>  
                </div>
                <div className="bottomText">
                    <h2>REVOLUTION TIME</h2>
                    <p>365 DAYS</p>
                </div>
                <div className="bottomText">
                    <h2>RADIUS</h2>
                    <p>6120km</p>
                </div>
                <div className="bottomText">
                    <h2>TEMPERATURE</h2>
                    <p>-49° to 59°C</p>
                </div>
            </div>
      </div>
    )
}

export default Coruscant